:root {
  --secondary: #f5f1f1;
}
.container {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 8px;
  padding: 8px;
  margin-top: 4px;
  box-sizing: border-box;
  background: #d2c6b2;
  border-radius: 8px;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #b9ab92;
  padding: 0 0 20px 0;
  border-radius: 8px;
}
.card .title {
  font-weight: bolder;
  color: var(--secondary);
  font-size: 1.3em;
  background: #998563;
  width: 100%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border-radius: 8px 8px 0px 0px;
  margin-bottom: 20px;
}
.countUp {
  font-weight: 900;
  font-size: 2em;
  color: rgb(0, 0, 0);
  margin-bottom: 20px;
}
.date {
  color: #1b1b1b;
}
.infected {
  border-bottom: 10px solid rgba(0, 0, 255, 0.6);
}
.recovered {
  border-bottom: 10px solid rgba(0, 255, 0, 0.6);
}
.deaths {
  border-bottom: 10px solid rgba(255, 0, 0, 0.6);
}
.active {
  border-bottom: 10px solid rgba(0, 0, 0, 0.6);
}
@media only screen and (max-width: 768px) {
  .container {
    width: 90%;
    grid-template-columns: 2fr;
  }
}
