* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #eae7d9;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.section {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
h2 {
  margin: 30px 0 0 0;
  text-align: center;
  color: rgb(136, 94, 94);
}
::-webkit-scrollbar {
  width: 10px;
  border-radius: 8px;
}

::-webkit-scrollbar-track {
  background: #fefefe;
}
::-webkit-scrollbar-thumb {
  background: #3d3c04;
}
:-webkit-scrollbar-thumb:hover {
  background: #000000;
}
